import React from "react"
import { ArrowDiv, PopUpLink } from "./styles"

interface IArrowModuleProps {
	icon: string
	list: { text: string; isLink?: boolean; linkOption?: number }[]
	name: string
	color: string
	moduleTitle: string
	openModal(id: number): void
}

export default function ArrowModule(props: IArrowModuleProps) {
	const { icon, list, name, color, openModal, moduleTitle } = props

	return (
		<div
			style={{
				flex: 1,
				flexDirection: "column",
				marginBottom: 20,
			}}
		>
			<div style={{ backgroundColor: color, width: 60, flex: 1 }}>
				<img src={icon} width="60" />
			</div>
			<div
				style={{
					borderLeft: `2px solid ${color}`,
					flex: 1,
					padding: 15,
					textAlign: "start",
					fontSize: 20,
					minHeight: 260,
				}}
			>
				<p style={{ fontSize: 15, flex: 1 }}>{moduleTitle}</p>
				<ul
					style={{
						listStylePosition: "inside",
						margin: 0,
						listStyleType: "square",
						flex: 1,
					}}
				>
					{list.map(item => {
						if (item.isLink) {
							return (
								<PopUpLink onClick={() => openModal(item.linkOption as number)}>
									<li key={item.text}>
										{item.text} <strong>+</strong>
									</li>
								</PopUpLink>
							)
						} else {
							return <li key={item.text}>{item.text}</li>
						}
					})}
				</ul>
			</div>
			<ArrowDiv color={color}>
				<p>{name}</p>
			</ArrowDiv>
		</div>
	)
}
