import React, { useState } from "react"
import Banner from "../../utils/Banner"
import ArrowModule from "./components"
import {
	ImageBackgroundSession,
	ButtonSlide,
	Description,
	ModuleWraper,
	Promotion,
	ModalContainer,
} from "./styles"
import Swiper from "react-id-swiper"
import "swiper/css/swiper.css"
import Expand from "react-expand-animated"
import Modal from "react-responsive-modal"
import { Link } from "react-scroll"

export default function NovosLeoesKivalita() {
	const [development, setDevelopment] = useState(false)
	const [quality, setQuality] = useState(false)
	const [showModal, setShowModal] = useState(false)
	const [modalList, setModalList] = useState(
		[] as { title: string; subList: string[] }[]
	)
	const [paramsSwiper, setParamsSwiper] = useState<object>({
		effect: "coverflow",
		grabCursor: false,
		centeredSlides: true,
		slidesPerView: 3,
		navigation: {
			nextEl: ".swiper-button-next",
			prevEl: ".swiper-button-prev",
		},
	})

	const expandDevelopment = () => {
		setDevelopment(!development)
	}

	const expandQuality = () => {
		setQuality(!quality)
	}

	const openModal = (option: number) => {
		switch (option) {
			case 2:
				setModalList([
					{
						title: "Validações e Qualificações",
						subList: [
							"Validação de Utilidades",
							"Validação de Transporte, Estudo de Cadeia fria e Cadeia Seca",
							"Validação de Sistemas Computadorizados",
							"Validação de Limpeza",
							"Validação de Metodologia Analítica",
							"Validação de processo",
							"Qualificação de Equipamentos ",
						],
					},
					{
						title: "Assuntos regulatório",
						subList: ["Documentação", "Auditorias", "Gestão de risco"],
					},
					{
						title: "Produção",
						subList: ["Garantia da qualidade em produção"],
					},
				])
				break
			case 3:
				setModalList([
					{
						title: "Controle de Qualidade",
						subList: [
							"Controle de qualidade Micro",
							"Controle de qualidade Físico Químico",
							"Validação de Metodologia Analítica",
						],
					},
				])
				break
			case 5:
				setModalList([
					{
						title: "",
						subList: ["Garantia da Qualidade em área hospitalar"],
					},
				])
				break
		}
		setShowModal(true)
	}

	const closeModal = () => setShowModal(false)

	const sessionImage = ""

	const projectImage =
		"https://www.kivalitaconsulting.com/wp-content/NovosLeoes/project.jpeg"

	const buyNow =
		"https://www.kivalitaconsulting.com/wp-content/NovosLeoes/last.jpeg"

	const slideButtonList = [
		{
			imageUrl:
				"https://www.kivalitaconsulting.com/wp-content/NovosLeoes/aluno.jpeg",
			text: "ALUNO",
		},
		{
			imageUrl:
				"https://www.kivalitaconsulting.com/wp-content/NovosLeoes/patrocinador.jpeg",
			text: "PATROCINADOR",
			url: "https://conteudo.kivalitaconsulting.com/pnlk-patrocinadores",
		},
		{
			imageUrl:
				"https://www.kivalitaconsulting.com/wp-content/NovosLeoes/empresa.jpeg",
			text: "EMPRESA RECRUTADORA",
			url: "https://conteudo.kivalitaconsulting.com/pnlk",
		},
	]

	const modules = [
		{
			icon:
				"https://www.kivalitaconsulting.com/wp-content/NovosLeoes/moduleIcon1.svg",
			list: [
				{
					text: "Noções da Qualidade",
				},
			],
			name: "MÓDULO 1",
			color: "#13414f",
			moduleTitle:
				"Nesta fase, o participante estará se capacitando profissionalmente com aulas teóricas e acompanhamento profissional de área, adquirindo conhecimentos técnicos e comportamentais para o mercado de trabalho.",
		},
		{
			icon:
				"https://www.kivalitaconsulting.com/wp-content/NovosLeoes/moduleIcon2.svg",
			list: [
				{
					isLink: true,
					linkOption: 2,
					text: "Garantia da Qualidade",
				},
				{
					isLink: true,
					linkOption: 3,
					text: "Laboratorial",
				},
				{
					isLink: true,
					linkOption: 5,
					text: "Área Hospitalar",
				},
			],
			name: "MÓDULO 2",
			color: "#1f6b82",
			moduleTitle:
				'Nesta fase, o participante estará adquirindo conhecimentos específicos da área de industria de forma "híbrida" (conhecimento teórico e prático), selecionando minimamente 2 areas para poder atuar.',
		},
		{
			icon:
				"https://www.kivalitaconsulting.com/wp-content/NovosLeoes/moduleIcon3.svg",
			list: [
				{
					text: "Estágio por bom desempenho na Kivalita",
				},
				{
					text: "Estágio em empresas recrutadoras ou fornecedores parceiros",
				},
			],
			name: "MÓDULO 3",
			color: "#13414f",
			moduleTitle:
				"Nesta ultima fase, dependendo do desempenho dos participantes, eles podem conseguir: Estagio/Vaga na área (voluntária ou remunerada), onde irá exercer todo o conhecimento que adquiriu.",
		},
	]

	const modulesDev = [
		{
			icon:
				"https://www.kivalitaconsulting.com/wp-content/NovosLeoes/moduleIcon1.svg",
			list: [
				{
					text: "Introdução às tecnologias de desenvolvimento de software",
				},
			],
			name: "MÓDULO 1",
			color: "#13414f",
			moduleTitle:
				"Nesta fase, o participante estará se capacitando profissionalmente com aulas teóricas e acompanhamento profissional de área, adquirindo conhecimentos técnicos e comportamentais para o mercado de trabalho.",
		},
		{
			icon:
				"https://www.kivalitaconsulting.com/wp-content/NovosLeoes/moduleIcon2.svg",
			list: [
				{
					text: "Desenvolvimento Back-End",
				},
				{
					text: "Desenvolvimento Front-End",
				},
			],
			name: "MÓDULO 2",
			color: "#1f6b82",
			moduleTitle:
				'Nesta fase, o participante estará adquirindo conhecimentos específicos da área de industria de forma "híbrida" (conhecimento teórico e prático), selecionando minimamente 2 areas para poder atuar.',
		},
		{
			icon:
				"https://www.kivalitaconsulting.com/wp-content/NovosLeoes/moduleIcon3.svg",
			list: [
				{
					text: "Estágio por bom desempenho na Kivalita",
				},
				{
					text: "Estágio em empresas recrutadoras ou fornecedores parceiros",
				},
			],
			name: "MÓDULO 3",
			color: "#13414f",
			moduleTitle:
				"Nesta ultima fase, dependendo do desempenho dos participantes, eles podem conseguir: Estagio/Vaga na área (voluntária ou remunerada), onde irá exercer todo o conhecimento que adquiriu.",
		},
	]

	const professors = [
		{
			name: "Bárbara Guelfi",
			position: "CEO da Kivalita Consulting",
			image:
				"https://www.kivalitaconsulting.com/wp-content/NovosLeoes/professor/Barbara.png",
		},
		{
			name: "Ana Carolina",
			position: "Analista da qualidade Kivalita Consulting",
			image:
				"https://www.kivalitaconsulting.com/wp-content/NovosLeoes/professor/AnaCarolina.png",
		},
		{
			name: "Tatyana Baptista",
			position: "Analista da qualidade Kivalita e pesquisadora",
			image:
				"https://www.kivalitaconsulting.com/wp-content/NovosLeoes/professor/tatyana.png",
		},
		{
			name: "Juliana Nunes",
			position: "Líder da qualidade na Kivalita Consulting",
			image:
				"https://www.kivalitaconsulting.com/wp-content/NovosLeoes/professor/juliana.png",
		},
		{
			name: "Newton Lopes",
			position: "Analista da Qualidade da Kivalita Consulting",
			image:
				"https://www.kivalitaconsulting.com/wp-content/NovosLeoes/professor/NewtonLopes.png",
		},
		{
			name: "Luiz Peres",
			position: "Consultor de Qualidade",
			image:
				"https://www.kivalitaconsulting.com/wp-content/NovosLeoes/professor/LuizPeres.png",
		},
	]

	const myRef = React.useRef(null)

	return (
		<ModalContainer>
			<div ref={myRef} />
			{showModal && (
				<Modal
					classNames={{ modal: "customModal" }}
					open={showModal}
					onClose={closeModal}
					center
					container={myRef.current}
				>
					{modalList && (
						<div style={{ minWidth: 200, maxWidth: 400 }}>
							{modalList.map(list => (
								<span key={list.title} style={{ textAlign: "start" }}>
									<h5>{list.title}</h5>
									<ul style={{ listStylePosition: "inside", marginLeft: 10 }}>
										{list.subList.map(item => (
											<li key={item}>{item}</li>
										))}
									</ul>
								</span>
							))}
						</div>
					)}
				</Modal>
			)}
			<Banner titlePage={"Projeto Novos Leões Kivalita"} />
			<ImageBackgroundSession
				imageUrl={sessionImage}
				style={{ paddingBottom: 25 }}
			>
				<Description>
					<h3>Eu sou:</h3>
					<hr />
					<p style={{ fontSize: 25 }}>
						<b>Aqui o Leão é VOCÊ</b>
					</p>
				</Description>
				{slideButtonList.map(button => {
					if (button.text === "ALUNO") {
						return (
							<Link to="Banner" smooth={true}>
								<ButtonSlide
									imageUrl={button.imageUrl}
									style={{ width: "40%", minWidth: 200, marginBottom: 0 }}
								>
									<div className="background-overlay"></div>
									<p className="buttonText">{button.text}</p>
								</ButtonSlide>
							</Link>
						)
					} else {
						return (
							<a key={button.text} href={button.url} target="__blank">
								<ButtonSlide
									imageUrl={button.imageUrl}
									style={{ width: "40%", minWidth: 200 }}
								>
									<div className="background-overlay"></div>
									<p className="buttonText">{button.text}</p>
								</ButtonSlide>
							</a>
						)
					}
				})}
			</ImageBackgroundSession>
			<ImageBackgroundSession
				imageUrl={projectImage}
				style={{
					textAlign: "center",
					padding: 20,
					minHeight: 450,
					display: "flex",
				}}
			/>
			<ImageBackgroundSession id="Banner" imageUrl={sessionImage}>
				<Description>
					<h3>Como funciona?</h3>
					<hr />
					<p style={{ fontSize: 20 }}>
						Você irá precisar passar por 3 fases para estar completamente
						capacitado para o mercado:
					</p>
				</Description>

				<div
					style={{
						display: "flex",
						padding: 10,
						flexDirection: "column",
						marginBottom: 20,
					}}
				>
					<Description>
						<h2>Noções da Qualidade</h2>
						<hr />
					</Description>

					<ModuleWraper>
						{modules.map(module => (
							<ArrowModule
								key={module.name}
								icon={module.icon}
								list={module.list}
								name={module.name}
								color={module.color}
								openModal={openModal}
								moduleTitle={module.moduleTitle}
							/>
						))}
					</ModuleWraper>
				</div>

				<div
					style={{
						display: "flex",
						padding: 10,
						flexDirection: "column",
						marginBottom: 20,
					}}
				>
					<Description>
						<h2>
							Desenvolvimento de Softwares <strong>(Em breve)</strong>
						</h2>
						<hr />
					</Description>
					<ModuleWraper>
						{modulesDev.map(module => (
							<ArrowModule
								key={module.name}
								icon={module.icon}
								list={module.list}
								name={module.name}
								color={module.color}
								openModal={openModal}
								moduleTitle={module.moduleTitle}
							/>
						))}
					</ModuleWraper>
				</div>
			</ImageBackgroundSession>
			<ImageBackgroundSession
				imageUrl={sessionImage}
				style={{ paddingBottom: 30 }}
			>
				<Description>
					<h3>Professores Ativos no mercado!</h3>
					<hr />
				</Description>
				<Swiper {...paramsSwiper}>
					{professors.map(prof => (
						<div
							key={prof.name}
							data-lightbox="about"
							style={{ textAlign: "center" }}
						>
							<div
								style={{
									height: "200px",
									background: "no-repeat 50%",
									backgroundSize: "contain",
									backgroundImage: `url(${prof.image})`,
									marginBottom: 10,
								}}
							/>
							<div>
								<strong>{prof.name}</strong>
							</div>
							<div>{prof.position}</div>
						</div>
					))}
				</Swiper>
			</ImageBackgroundSession>
			<ImageBackgroundSession
				imageUrl={buyNow}
				style={{
					textAlign: "center",
					padding: 40,
					minHeight: 300,
					display: "flex",
					flexDirection: "column",
				}}
			>
				<Promotion>
					<p style={{ flex: 1, fontSize: 25 }}>
						A <strong>oportunidade</strong> que você tem para estar a frente e
						se destacar no mercado!
					</p>
					<p style={{ flex: 1, fontSize: 25 }}>
						São mais de &nbsp;
						<strong>
							1500 vagas na área de qualidade e 30.000 vagas na área de
							tecnologia
						</strong>
						&nbsp; disponíveis só no Linkedin!
					</p>

					<div className="buttons">
						<a
							href="https://novosleoes.kivalitaconsulting.com"
							target="__blank"
						>
							<ButtonSlide className="button" imageUrl={sessionImage}>
								<div className="background-overlay"></div>
								<p
									className="buttonText"
									style={{ paddingLeft: 25, paddingRight: 25 }}
								>
									Saiba mais sobre o Projeto Novos Leões Kivalita
								</p>
							</ButtonSlide>
						</a>
						<a
							href="https://novosleoes.kivalitaconsulting.com/courses/noces-gerais-da-qualidade-barra-desconto-promocional-de-20-porcento-ate-dia-23-04-2021"
							target="__blank"
						>
							<ButtonSlide className="button" imageUrl={sessionImage}>
								<div className="background-overlay"></div>
								<p className="buttonText">Quero me inscrever AGORA!</p>
							</ButtonSlide>
						</a>
					</div>
				</Promotion>
			</ImageBackgroundSession>
		</ModalContainer>
	)
}
