import React from "react"
import Layout from "../components/Layout"
import NovosLeoesKivalita from "../components/pages/NovosLeoesKivalita"
import SEO from "../components/utils/SEO"
import { IBreadCrumbOption } from "../interfaces"

export default function NovosLeoes() {
	const breadCrumbList: IBreadCrumbOption[] = [
		{
			url: "/",
			name: "Home",
			isActive: true,
		},
		{
			url: "novos-leoes-kivalita",
			name: "Projeto Novos Leões Kivalita",
			isActive: false,
		},
	]

	return (
		<Layout breadCrumbList={breadCrumbList}>
			<SEO
				title="Projeto Novos Leões Kivalita"
				titleMetaDescription="Projeto Novos Leões Kivalita"
				description="Buscando soluções em qualidade? A Kivalita Consulting pode te ajudar! Conheça os nossos serviços e cases de sucesso para atender o seu negócio."
			/>
			<NovosLeoesKivalita />
		</Layout>
	)
}
